@import url(https://fonts.googleapis.com/css2?family=Rubik&display=swap);
:root{
  --main-bg: rgba(255, 255, 255, 0.1);
  --font-color: #ffffff;
  --fontFamily-main: 'Rubik', sans-serif;
}
body {
  color: #ffffff;
  color: var(--font-color);
  margin: 0;
  font-family: 'Rubik', sans-serif;
  font-family: var(--fontFamily-main);
  font-style: normal;
  font-weight: normal;
}
img {
  display: block;
  max-width: 100%;
  height: auto;
}
ul {
  list-style: none;
  margin: 0;
  padding-left: 0;
}
p{
  margin: 0;
}

::-webkit-scrollbar {
  width: 12px;
}

::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px #ffffff;
  -webkit-box-shadow: inset 0 0 6px var(--font-color); 
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px #ffffff;
  -webkit-box-shadow: inset 0 0 6px var(--font-color); 
}
